import React, { useEffect } from 'react';

import './StudentListUI.css';
import $ from 'jquery';

const StudentListUI = ( props ) => {

    useEffect(
        () => {

            $('.LineBreak').last().css('display', 'none');

        }, []
    )

    return (
        <div className="StudentListUI">
            <div className="StudentListUI-Content">
                <div className="upper">
                    <h3>Students'List</h3>
                    <div className="control">
                        <div>
                            <input type="search" className="searchInputs" placeholder="Search Keywords" />
                        </div>
                        <div></div>
                        <div>
                            <div className="icons" title="Add New Student" onClick={ props.NewStudent }>
                                <i className="las la-user-plus"></i>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="studentList">

                    {
                        props.Students
                        ?
                            props.Students.length === 0
                            ?
                            <h4>No Student Found</h4>
                            :
                            props.Students.map(
                                ( val, index, arr ) => {

                                    let d = new Date( val.recording_date );

                                    function tConvert (time) {
                                        // Check correct time format and split into components
                                        time = time.toString ().match (/^([01]\d|2[0-3])(:)([0-5]\d)(:[0-5]\d)?$/) || [time];
                                      
                                        if (time.length > 1) 
                                        { // If time format correct
                                          time = time.slice (1);  // Remove full string match value
                                          time[5] = +time[0] < 12 ? 'AM' : 'PM'; // Set AM/PM
                                          time[0] = +time[0] % 12 || 12; // Adjust hours
                                        }
                                        return time.join (''); // return adjusted time or original string
                                    }

                                    return (
                                        <div key={ index }>
                                            <div className="Student" style={ { animationDelay: '.' + index + 's' } } key={ index }>
                                                <div className="id">
                                                    <p>Registration</p>
                                                    <p>{ val.student_id }</p>
                                                </div>
                                                <div>
                                                    <h6>{ val.student_name }</h6>
                                                    <p>{ val.residential_address }</p>
                                                </div>
                                                <div>
                                                    <h6>Recording Date</h6>
                                                    <p>{ d ? d.toString().substring(0,15) + ' at ' + tConvert( val.recording_time ) : null }</p>
                                                </div>
                                                <div>
                                                    <div className="icons" title="Edit Record" onClick={ () => props.EditStudent( val.student_id ) }>
                                                        <i className="las la-edit"></i>
                                                    </div>
                                                    <div className="icons" title="Delete Record">
                                                        <i className="las la-trash"></i>
                                                    </div>
                                                </div>
                                                <div onClick={ () => props.EditStudent( val.student_id ) }>
                                                    <i className="las la-graduation-cap"></i>
                                                </div>
                                            </div>
                                            <div className={ arr.length - 1 === index ? "LineBreak d-none" : "LineBreak" }>
                                                <div>
                                                    <div className="line"></div>
                                                </div>
                                                <div></div>
                                            </div>
                                        </div>
                                    )

                                }
                            )
                        :
                        null
                    }

                </div>
            </div>
        </div>
    )

}

export default StudentListUI;